
import Toast from 'vux/src/components/toast/index.vue'

import axios from 'axios'
import { debug } from 'util';
export default {
name: 'app-mine-rebuild-box',
data() {
return {
rebuildInfo: {
password: '',
checkPassword: '',
token: window.localStorage.getItem('token')
}
}
},
methods: {
handleRebuildClick(params) {
if (params.newpassword.length < 6 || params.newpassword.length > 20) {
this.$vux.toast.show({
type: 'cancel',
text: '密码长度设置6-20个字符'
})
return 0
} else if (this.rebuildInfo.password !== this.rebuildInfo.checkPassword) {
this.$vux.toast.show({
type: 'cancel',
text: '两次密码输入不一致'
})
return 0
} else {
axios
.post('http://api.zhimaim.com/api/user/forget_password_3', {
newpassword: this.rebuildInfo.password,
confirmpassword: this.rebuildInfo.checkPassword,
token: this.rebuildInfo.token
})
.then(res => {
if (res.data.success === true) {
console.log(res.data);
this.$vux.toast.show({
text: '密码修改成功'
})
this.$router.push({ name: 'appMineLogin' });
} else {
this.$vux.toast.show({
type: 'cancel',
text: '密码修改失败'
})
}
})
.catch(res => {
this.$vux.toast.show({
type: 'warn',
text: '不知名的网络问题！'
})
throw res
})
}
}
}
}



